import { get, getDatabase, limitToFirst, orderByKey, query, ref, startAt } from 'firebase/database';
import { useEffect, useState } from 'react';

export default function useVideoList(page) {
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(false);
    const [videos, setVideos] = useState([]);
    const [hasMore, setHasMore] = useState(true);
    useEffect(() => {
        async function fetchVideos() {
            // database realated works
            const db = getDatabase();
            const videoRef = ref(db, 'videos');
            // akta number k string korar niyom("" + page or oi number name)
            const videoQuery = query(videoRef, orderByKey(), startAt(`${page}`), limitToFirst(6));
            try {
                setError(false);
                setLoading(true);
                // request firebase database
                const snapShot = await get(videoQuery);
                setLoading(false);
                if (snapShot.exists()) {
                    setVideos((prevVideos) => [...prevVideos, ...Object.values(snapShot.val())]);
                } else {
                    setHasMore(false);
                }
            } catch (err) {
                console.log(err);
                setError(true);
                setLoading(false);
            }
        }
        // setTimeout(() => {
        //     fetchVideos();
        // }, 1500);
        fetchVideos();
    }, [page]);
    return {
        loading,
        error,
        videos,
        hasMore,
    };
}
